body {
  overflow: hidden;
  margin: 0px !important;
  padding: 0px !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #efefef;
}

.visible {
  visibility: visible;
}

.invisible {
  visibility: hidden;
}

.flex-row {
  display: flex;
  align-items: center;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-fe {
  justify-content: flex-end;
}
.flex-jc-c {
  justify-content: center;
}
.flex-grow-unset {
  flex-grow: unset !important;
}
.w-100 {
  width: 100% !important;
}

.no-margin {
  margin: 0px !important;
}

.link-custom {
  color: #444;
  cursor: pointer;
  font-weight: bold;
}

.monospace-font {
  font-family: monospace, monospace;
}

.scrollable-segment {
  height: 550px;
  overflow-y: scroll;
}

.custom-icon.circular {
  border: 1px solid #ddd;
  box-shadow: unset !important;
}

.custom-icon.mgreen {
  color: #CDDC39 !important;
}
.custom-icon.gray {
  color: #ddd !important;
}

.pt-5 {
  padding-top: 5px;
}
.pt-10 {
  padding-top: 10px;
}

.mt-50 {
  margin-top: 50px !important;
}
.mt-10 {
  margin-top: 10px !important;
}


/* Generic classes end */

.ui.progress .bar {
  min-width: 0 !important;
}

.ui.active.progress .bar {
  min-width: 0 !important;
}

/* Framework override ends */

.login-main-container {
  background-color: #efefef;
  height: 100vh;
  width: 100vw;
}

.login-service-info .status-icon{
  margin-right: 5px;
}

.login-card {
  min-width: 500px;
}

.login-card .header {
  font-size: 2em !important;
  text-align: center;
  padding-bottom: 30px;
}

.login-card .content-middle-container{
  margin: 0px 120px 0px 120px;
}

.login-card .content-middle-container.content-extra{
  margin-top: 20px;
  margin-bottom: 20px;
}

.login-card .content-middle-container .description{
  margin-bottom: 60px;
}

.logo-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;
}

.app-name-title span:nth-child(2) {
  font-weight: 100 !important;
  color: rgb(41, 182, 246);
}

.app-name-title span:nth-child(1) {
  font-weight: bold;
}

.menu-vertical-home {
  height: 100vh;
}

.navigation-bar {
  padding: 0px 35px 5px 10px;
  margin-top: 0px !important;
  margin-bottom: 0px !important;
  background-color: #fff;
}

.content-section {
  padding: 20px;
  width: 100%;
  background-color: #efefef;
}

.content-panel {
  display: flex;
  justify-content: flex-start;
}

.alert-message-login {
  visibility: visible;
  display: block !important;
  height: 47px !important;
}

.alert-message-login.invisible {
  visibility: hidden;
}

.list-item-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 0px 5px 0px;
}

.card-chart-container {
  width: 70%;
  padding-top: 20px;
  padding-bottom: 20px;
  margin: 0 auto;
}

.header-with-label {
  display: flex !important;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.dashboard-cards-group .card {
  width: 100% !important;
  margin-right: 0px !important;
  margin-left: 0px !important;
  flex-grow: 1;
}

.dashboard-cards-group {
  margin-left: 0px !important;
  margin-right: 0px !important;
  display: flex;
  flex-direction: column;
  justify-content: space-between !important;
}

.grid-full-height {
  height: 100vh;
}

.status-icon {
  display: block;
  height: 10px;
  width: 10px;
  border-radius: 50%;
}
.status-text-item {
  margin-left: 5px;
}

.green-bg {
  background-color: rgb(118, 255, 3);
}
.red-bg {
  background-color: rgb(244, 67, 54);
}
.gray-bg {
  background-color: rgb(120, 144, 156);
}

.card-icon-container {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin: 20px 0px 20px 0px;
}

.service-down-segment-left,
.service-down-segment-right {
  padding: 0px !important;
}

.service-down-segment-left .card,
.service-down-segment-right .card {
  width: 550px !important;
}

.status-progress-bar {
  display: flex;
  align-items: center;
  width: 100%;
}

.status-progress-bar .progress {
  width: 100%;
}

.status-progress-bar .label {
  margin-left: 10px !important;
  width: 170px;
}

.add-node-form-body {
  width: 60%;
  margin: 0 auto;
}

.add-node-form-body div {
  margin: 10px 0px 0px 0px;
}

.add-node-form-body div .ui.label.label {
  width: 160px;
}

.loader-progress-bar {
  width: 100%;
  justify-content: center;
  display: flex;
  margin: 100px 0px 100px 0px;
}

.loader-progress-bar .ui.progress {
  width: 350px;
}

.loader-progress-bar .ui.progress .label {
  font-weight: normal;
  color: #444;
  margin-top: 10px;
}
.dashboard-page .ui.list .header {
  margin-bottom: 10px !important;
  color: #2185d0!important;
}
.ui.secondary.navigation-bar.menu {
  background-color: #fff;
}
